.heading {
    display: flex;
    align-items: center;
    justify-content: center;
}

.heading div {
    background-color: rgba(39, 39, 39, 0.274);
    height: 1px;
    width: 30vw;
}

.heading p {
    margin: 3vmax;
    color: rgba(77, 77, 77);
    font-size: 2.5vmax;
    font-family: Calibri, 'Gill Sans', 'Gill Sans MT', 'Trebuchet MS', sans-serif;
    font-weight: 100;
}


@media only screen and (max-width:768) and (min-width:425){
    .heading p{
        text-align: center;
    }
}

@media only screen and (max-width:425px) and (min-width:250px) {
.heading p {
        text-align: center;
    }
}